import React from 'react';
import "./catalogCart.scss"
import PropTypes from 'prop-types'

const CatalogCard = ({full_name, html_url, description, language, license, stargazers_count, open_issues}) => {
    return (
        <li className="repositories-item">
            <div className="repositories-item__icon">
                <svg height="16" viewBox="0 0 16 16" version="1.1" width="16" aria-hidden="true">
                    <path
                        d="M2 2.5A2.5 2.5 0 014.5 0h8.75a.75.75 0 01.75.75v12.5a.75.75 0 01-.75.75h-2.5a.75.75 0 110-1.5h1.75v-2h-8a1 1 0 00-.714 1.7.75.75 0 01-1.072 1.05A2.495 2.495 0 012 11.5v-9zm10.5-1V9h-8c-.356 0-.694.074-1 .208V2.5a1 1 0 011-1h8zM5 12.25v3.25a.25.25 0 00.4.2l1.45-1.087a.25.25 0 01.3 0L8.6 15.7a.25.25 0 00.4-.2v-3.25a.25.25 0 00-.25-.25h-3.5a.25.25 0 00-.25.25z"></path>
                </svg>
            </div>
          <div className="repositories-item__content">
              <a className="repositories-item__title"
                 href={html_url}>{full_name}</a>
              <p className="repositories-item__description">
                  {description}
              </p>
              <ul className="repositories-item__info">
                  <li>
                      <div className="repositories-item__star" >
                          <svg height="16"  viewBox="0 0 16 16"
                               version="1.1" width="16" aria-hidden="true">
                              <path fillRule="evenodd"
                                    d="M2 2.5A2.5 2.5 0 014.5 0h8.75a.75.75 0 01.75.75v12.5a.75.75 0 01-.75.75h-2.5a.75.75 0 110-1.5h1.75v-2h-8a1 1 0 00-.714 1.7.75.75 0 01-1.072 1.05A2.495 2.495 0 012 11.5v-9zm10.5-1V9h-8c-.356 0-.694.074-1 .208V2.5a1 1 0 011-1h8zM5 12.25v3.25a.25.25 0 00.4.2l1.45-1.087a.25.25 0 01.3 0L8.6 15.7a.25.25 0 00.4-.2v-3.25a.25.25 0 00-.25-.25h-3.5a.25.25 0 00-.25.25z"></path>
                          </svg>
                          {stargazers_count}
                      </div>
                  </li>
                  <li>
                    <span className="repositories-item__color"></span>
                    <div className="repositories-item__language">{language}</div>
                  </li>
                  {
                      license ? <li>
                          <div> {license.name}</div>
                      </li> : null
                  }
                  <li>
                      <div>
                          {open_issues} issues
                          need help
                      </div>
                  </li>
              </ul>
          </div>
        </li>
    )
}

CatalogCard.propTypes = {
    full_name: PropTypes.string,
    html_url: PropTypes.string,
    description: PropTypes.string,
    language: PropTypes.string,
    license: PropTypes.object,
    stargazers_count: PropTypes.number,
    open_issues: PropTypes.number,
}

export default CatalogCard

